<template>
    <v-form>
        <v-card flat class="ma-5 grey lighten-5">
            <v-card-title>
                <v-spacer></v-spacer>
                <span class="subheading cairo">التسويق بالعموله</span>
                <v-spacer></v-spacer>
            </v-card-title>
            <v-card-text>
                <v-layout row wrap>
                    <v-flex
                    xs12
                    md4
                    >
                        <v-text-field
                            v-model="firstName"
                            :rules="firstNameRules"
                            :counter="20"
                            required
                            placeholder="اسم الاول *"
                            class="ma-4"
                        ></v-text-field>
                    </v-flex>
                    <v-flex
                    xs12
                    md4
                    >
                        <v-text-field
                            v-model="lastName"
                            :rules="lastNameRules"
                            :counter="20"
                            required
                            placeholder="اسم الثاني *"
                            class="ma-4"
                        ></v-text-field>
                    </v-flex>
                    <v-flex
                    xs12
                    md2
                    >
                        <v-select
                        v-model="acount"
                        :items="acountType"
                        label="نوع الحساب *"
                        required
                        class="ma-4"
                        ></v-select>
                    </v-flex>
                    <v-flex
                    xs12
                    md2
                    >
                        <v-select
                        v-model="resCountry"
                        :items="residenceCountry"
                        label="مكان الاقامه *"
                        required
                        class="ma-4"
                        ></v-select>
                    </v-flex>
                    <v-flex
                    xs12
                    md4
                    >
                        <v-text-field
                            v-model="phone"
                            :rules="phoneRules"
                            :counter="15"
                            required
                            placeholder="رقم الجوال *"
                            class="ma-4 black--text"
                        ></v-text-field>
                    </v-flex>
                    <v-flex
                    xs12
                    md4
                    >
                    <v-text-field
                        v-model="email"
                        :role="emailRules"
                        :counter="15"
                        placeholder="البريد الالكتروني *"
                        class="ma-4"
                    ></v-text-field>
                    </v-flex>
                    <v-flex
                    xs12
                    md4
                    >
                        <v-text-field
                            v-model="website"
                            :rules="websiteRules"
                            :counter="15"
                            required
                            placeholder="الموقع الالكتروني *"
                            class="ma-4 black--text"
                        ></v-text-field>
                    </v-flex>
                </v-layout>
            </v-card-text>
            <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn flat color="success" @click="submit"><span class="cairo">حفظ البيانات</span></v-btn>
                <v-btn flat color="red" @click="reset"><span class="cairo">مسح الحقول</span></v-btn>
                <v-spacer></v-spacer>
            </v-card-actions>
        </v-card>
    </v-form>
</template>

<script>
// import MarketingServec from '../../services/MarketingServec'
// import axios from 'axios'

export default {
    name: 'CardOrderForm',
    props: ['orderType'],
    data(){
        return {
            valid: false,
            menu: false,
            select: null,
            select1: null,
            firstName: '',
            firstNameRules: [
                v => !!v || '  الاسم مطلوب',
                v => v.length <= 200 || 'يجب ان يكون الاسم اقل من 200 حرف'
            ],
            lastName: '',
            lastNameRules: [
                v => !!v || '  الاسم مطلوب',
                v => v.length <= 200 || 'يجب ان يكون الاسم اقل من 200 حرف'
            ],
            phone: '',
            phoneRules: [
                v => !!v || '  رقم الجوال مطلوب',
                v => v.length <= 15 || 'يجب ان يكون الاسم اقل من 15 حرف'
            ],
            website: '',
            websiteRules: [
                v => !!v || 'الجنسيه مطلوبه',
                v => v.length <= 20 || 'يجب ان تكون الجنسيه اقل من 20 حرف'
            ],
            resCountry: '',
            residenceCountry: [
                'السعوديه',
                'مصر',
                'الاردن'
            ],
            acount: '',
            acountType: [
                'مسوق',
                'مسوق2',
                'مسوق3'
            ],
            email: '',
            emailRules: [
                v => !!v || 'البريد الالكتروني اختياري',
                v => v.length <= 200 || 'يجب ان يكون البريد الالكتروني اقل من 200 حرف'
            ],
        }
    },
    methods: {
        save (date) {
            this.$refs.menu.save(date)
        },
        submit () {
            // const markMan = {
            //     firstName : this.firstName,
            //     lastName : this.lastName,
            //     phone: this.phone,
            //     email: this.email,
            //     acountType: this.acount,
            //     residenceCountry: this.resCountry,
            //     website: this.website,
            // }
            // console.log(markMan)
            this.select1 = null
            this.select = null
            // MarketingServec.create(markMan)
            // .then(res => { 
            //     console.log(res.data)
            //     this.reset();
            // })
            // .catch(error => console.log(error))
        },
        reset () {
            this.firstName = '';
            this.lastName = '';
            this.phone = '';
            this.email = '';
            this.acount = '';
            this.resCountry = '';
            this.website = '';
            // this.select1 = null
            // this.select = null
    }
    },
    watch: {
        menu (val) {
            val && setTimeout(() => (this.$refs.picker.activePicker = 'YEAR'))
        }
    },
}
</script>